.datetimepicker {
  border: 1px solid #e1e1e1;
  background: #fff;
  border-radius: 2px;
  text-align: left;
  position: absolute;
  top: 40px;
  left: 0;
  z-index: 1000;
  cursor: default;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.15);
}
.datetimepicker-header {
  border-bottom: 1px solid #e6e6e6;
  padding: 6px 8px;
  height: 42px;
}
.datetimepicker-header > button {
  display: inline-block;
  margin: 2px 5px 0 0;
  vertical-align: top;
  color: #333;
  background: #fff;
  border: 1px solid #ccc;
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.05);
  cursor: pointer;
  outline: 0 none;
  padding: 3px 12px;
  text-align: center;
}
.datetimepicker-header > button:hover,
.datetimepicker-header > button.active {
  background: #4492d4;
  color: #fff;
  border-color: #2b78ba;
}
.datetimepicker-body {
  position: relative;
  padding-bottom: 11px;
}
.datetimepicker-body > table {
  float: left;
  margin: 0 8px;
  padding: 4px;
  border-spacing: 0 7px;
  border-collapse: collapse;
}
.datetimepicker-body > table > caption {
  height: 35px;
  line-height: 35px;
  font-weight: 700;
  font-size: 14px;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
  text-align: center;
  white-space: nowrap;
  min-width: 32px;
}
.datetimepicker-body > table thead tr {
  border: 1px solid #e1e1e1;
  background: #f7f8fa;
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.1);
}
.datetimepicker-body > table thead tr > th {
  padding: 5px 10px;
  line-height: 20px;
  color: #444;
  width: 20px;
  height: 20px;
}
.datetimepicker-body > table td {
  padding: 5px 9px;
  width: 20px;
  height: 20px;
  line-height: 20px;
  color: #333;
  text-align: center;
  transition: all .3s;
}
.datetimepicker-body > table td.disabled {
  color: #bbb;
  cursor: default;
}
.datetimepicker-body > table td.today {
  font-weight: bold;
  color: #4492d4;
}
.datetimepicker-body > table td.selected {
  background: #eee;
}
.datetimepicker-body > table td.active {
  background: #4492d4;
  color: #fff;
  border-radius: 4px;
}
.datetimepicker-body > table td.first {
  background: #bbbbbb;
  color: #fff;
  border-radius: 4px 0 0 4px;
}
.datetimepicker-body > table td.first.last {
  border-radius: 4px;
}
.datetimepicker-body > table td.last {
  background: #bbbbbb;
  color: #fff;
  border-radius: 0 4px 4px 0;
}
.datetimepicker-prev-month,
.datetimepicker-next-month {
  position: absolute;
  top: 10px;
  text-decoration: none;
  font-size: 20px;
  color: #666;
  -webkit-font-smoothing: antialiased;
}
.datetimepicker-prev-month:hover,
.datetimepicker-next-month:hover {
  color: #4492d4;
}
.datetimepicker-prev-month {
  left: 10px;
}
.datetimepicker-next-month {
  right: 10px;
}
.datetimepicker-footer {
  border-top: 1px solid #e6e6e6;
  padding: 6px 8px;
  height: 42px;
}
.datetimepicker-msg {
  float: left;
}
.datetimepicker-msg .form-control {
  display: inline-block;
  padding: 0 7px;
  vertical-align: middle;
  width: 90px;
  height: 28px;
  border-color: #bfbfbf!important;
  transition: border 0.2s linear 0s, box-shadow 0.2s linear 0s;
}
.datetimepicker-btns {
  float: right;
}
.datetimepicker-btns > .btn {
  margin-left: 5px;
}
.datetimepicker-dropdown {
  display: inline-block;
  margin: 0 5px;
  position: relative;
  vertical-align: top;
  cursor: pointer;
}
.datetimepicker-dropdown i {
  margin: 0 5px;
  vertical-align: middle;
  font-size: 12px;
  opacity: 0.5;
  filter: alpha(opacity=50);
}
.datetimepicker-dropdown.active {
  color: #4492d4;
}
.datetimepicker-dropdown.active > ul {
  display: block;
}
.datetimepicker-dropdown.active i {
  opacity: 1;
  filter: alpha(opacity=100);
}
.datetimepicker-dropdown > ul {
  display: none;
  position: absolute;
  top: 32px;
  left: 0;
  margin: 0;
  padding: 0;
  border: 1px solid #e1e1e1;
  border-radius: 0 0 4px 4px;
  background: #fff;
  max-height: 180px;
  overflow-y: auto;
  list-style: none;
  white-space: nowrap;
  color: #666;
}
.datetimepicker-dropdown > ul > li {
  text-align: center;
  padding: 0 30px 0 20px;
  cursor: pointer;
}
.datetimepicker-dropdown > ul > li.active,
.datetimepicker-dropdown > ul > li.active:hover {
  background: #6daade;
  color: #fff;
}
.datetimepicker-dropdown > ul > li:hover {
  background: #eee;
}
