/*
//@import "_config";          //项目样式设定项
//@import "_mix";             //项目样式工具

@laydate-color: @domColor6;
@laydate-color-orange: @domColor5;
@laydate-color-blue: @domColor1;
@laydate-color-green: @domColor2;
@laydate-color-red: @domColor3;


// body
.laydate_body {
    // 日历控件样式
    .laydate_box{
        width: 240px;
        font:12px '\5B8B\4F53',simsun;
        z-index:99999999;
        *margin:-2px 0 0 -2px;
        *overflow:hidden;
        background: #ffffff;
        box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
        color:  @laydate-color;

        // 清除容器内所有元素外边距
        *{
            margin: 0;
            padding: 0;
            .border-box(content-box);
        }

        // 重置li
        li{
            list-style: none;
        }

        // 链接
        a{
            text-decoration: none;
            blur:~"expression(this.onFocus=this.blur())";
            cursor:pointer;
            color:  @laydate-color;

            &:hover{
                text-decoration: none;
                color: #666;
            }
        }

        cite, label{
            position:absolute;
            width:0;
            height:0;
            border: 5px dashed transparent;
            overflow:hidden;
            cursor:pointer;
        }

        // 输入框
        input{
            float:left;
            height:24px;
            line-height:24px;
            text-align:center;
            border:none;
            cursor:pointer;
            color: #fff;
            outline:0;
            font-size:14px;
            background-color:#fff;
        }

        // 不可点击文字
        .laydate_void{
            cursor: ~"text!important";
            color: lighten( @laydate-color,5%);
        }

        .laydate_yms,.laydate_time{
            display: none;
        }

        #laydate_hms,.laydate_time {
            border:1px solid #ccc;
        }

        .laydate_ym .laydate_yms, .laydate_time {
            box-shadow: 2px 2px 5px rgba(0,0,0,.1);
        }

        // 显示元素
        .laydate_show{
            display: block;
        }

        // 已选
        .laydate_click{
            background-color:@laydate-color;
            color:#fff;
        }

        // 顶部栏
        .laydate_top{
            position: relative;
            height: 26px;
            padding: 5px;
            *width: 100%;
            z-index: 99;
            border-top:1px solid @laydate-color;
            background-color:@laydate-color;
        }

        // 选择器
        .laydate_yms{
            border:1px solid @laydate-color;
            background-color:@laydate-color;
            color:#fff;

            .laydate_chdown{
                border-top:1px solid darken(@laydate-color, 1%);
                border-bottom:none;
            }
        }

        // 年月选择器
        .laydate_ym{
            position: relative;
            float: left;
            height: 24px;
            cursor: pointer;
            border-top:1px solid @laydate-color;
            background-color:@laydate-color;

            // 年月弹层选择器
            .laydate_yms {
                position:absolute;
                left: -1px;
                top: 24px;
                height:181px;
            }

            label{
                right: 28px;
                top:50%;
                margin:-2px 0 0 -5px;
                border-top-style:solid;
                border-top-color: #fff;
            }

            input{
                background: transparent;
            }
        }

        // 年选择器
        .laydate_y {
            width: 121px;
            margin-right: 6px;

            input{
                width: 64px;
                margin-right: 15px;
                font-family: simsun;
            }

            .laydate_yms {
                width: 121px;
                text-align: center;

                a {
                    position: relative;
                    display: block;
                    height: 20px;

                    background: lighten(@laydate-color, 3%);

                    border-bottom:1px solid darken(@laydate-color, 1%);;

                    &:hover{
                        background: lighten(@laydate-color, 5%);
                    }
                }

                ul{
                    padding: 0;
                    height: 139px;
                    *overflow: hidden;
                    li {
                        float: left;
                        width: 60px;
                        height: 20px;
                        line-height: 20px;
                        text-overflow: ellipsis;
                        overflow: hidden;
                        word-spacing: nowrap;

                        &:hover{
                            background: lighten(@laydate-color, 10%);
                            color: #fff;
                        }
                    }
                }
            }
        }

        // 月选择器
        .laydate_m{
            width: 99px;

            .laydate_yms{
                padding: 0;
                width: 99px;

                span{
                    display: block;
                    float: left;
                    width: 42px;
